import React from 'react';
import './Popup.css';
import trading from '../assets/trading.jpg'
function Popup({ isOpen, onClose }) {


  return (
  <div className="trading container d-flex justify-content-center align-items-center vh-100">
  <div className="text-center">
    <img src={trading} width="50%" alt="Image" className="img-fluid" />
    <h2 className="mt-3">Trading has not yet started!!</h2>
    <p className="lead">Please be advised that trading has not yet begun for the day. If you are attempting to place a trade, please wait until trading commences before doing so. Thank you for your patience and understanding.</p>
    {/* <button className="btn btn-primary">Learn More</button> */}
  </div>
</div>

  );
}

export default Popup;
