import React from "react";
// const storedValue = localStorage.getItem("users");
// const str = JSON.parse(storedValue);
function generateTable(data, tableLimit,rowsPerTable) {
  
  const numRows = Math.ceil(data.length / (tableLimit * rowsPerTable));
  let currentRow = 0;
  let html = '';
  let lastTable = '';
  for (let i = 0; i < numRows; i++) {
    currentRow++;
    html += '<div class="table-row">';

    for (let j = 0; j < tableLimit; j++) {
      const start = i * tableLimit * rowsPerTable + j * rowsPerTable;
      const end = start + rowsPerTable;
      const tableData = data.slice(start, end);

      if (tableData.length === 0) {
        break;
      }

      html += '<table class="table">';
      html += '<thead><tr><th>ID</th><th>Name</th></tr></thead>';
      html += '<tbody>';

      for (const rowData of tableData) {
        html += '<tr>';
        html += `<td>${rowData.id}</td>`;
        html += `<td>${rowData.name}</td>`;
        html += '</tr>';
      }

      // Add empty cells to fill remaining space
      const lastRow = tableData[tableData.length - 1];
      const numCells = Object.keys(lastRow).length;
      if (numCells < 2) {
        for (let k = numCells; k < 2; k++) {
          html += '<td></td>';
        }
      }

      html += '</tbody>';
 // Add tfoot with sum of id
//  const idSum = tableData.reduce((acc, cur) => acc + cur.id, 0);
//  html += `<tfoot><tr><td>Total:</td><td>${idSum}</td></tr></tfoot>`;

      html += '</table>';
   // Keep track of last table
   if ((i === numRows - 1) && (j === tableLimit - 1)) {
    lastTable = html;
  }
      if (j === tableLimit - 1) {
        html += '<br>';
      }
    }

    html += '</div>';
  }
  const sum = data.reduce((acc, val) => acc + val.id, 0);


  const newWindow = window.open('');
  newWindow.document.write(`
    <html>
      <head>
        <title>Generated Table</title>
        <style>
          .table-row {
            align-items: start;
            display: flex;
            flex-wrap: wrap;
          }
          .table {
            margin: 10px;
          }
          .table th,
          .table td {
            padding: 5px;
          }
        </style>
      </head>
      <body>
      <h2>Valuation For Sale No 36</h2>
        <div id="table-container">
          ${html}
        </div>
        <table class="table">
            <tfoot>
              <tr>
                <th>Total:</th>
                <td>${sum}</td>
              </tr>
            </tfoot>
          </table>
      </body>
    </html>
  `);
}
const ValuationReport = ({ data }) => {
  const handleClick = () => {
    generateTable(data)
  };

  return <button className="btn btn-warning" onClick={()=>generateTable(data, 4,10)}>Valuation</button>;
};

export default ValuationReport;
